import React from 'react';
import Grid from '@material-ui/core/Grid';
import { styled } from '@material-ui/core/styles';
import Dialog from '@material-ui/core/Dialog';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import SvgIcon from '@material-ui/core/SvgIcon';
import CloseIcon from '@material-ui/icons/Close';
import Slide from '@material-ui/core/Slide';
import Hidden from '@material-ui/core/Hidden';
import ImageInspector from './ImageInspector';
import Render3D from './RenderCanvas';
import ImageGallery from './ImageGallery';


const MyAppBar = styled(AppBar)({
    background: 'linear-gradient(45deg, #007eff 30%, #220c10 75%)',
    border: 0,
    boxShadow: '0 3px 5px 2px rgba(34, 12, 16, .3)',
    color: 'white',
    padding: '0 2px',
    position: "relative",
});

const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
});

export default function CardDetail(props) {
    if (props.item !== null) {
        var item = props.item.tile;
        console.log(item);
        return (
            <div>
                <Dialog fullScreen open={props.open} onClose={props.handleClose} TransitionComponent={Transition}>
                    <MyAppBar>
                        <Toolbar>
                            <IconButton edge="start" color="inherit" onClick={props.handleClose} aria-label="close">
                                <CloseIcon />
                            </IconButton>
                            <h3 style={{ margin: 0 }}>{item.title}</h3>
                        </Toolbar>
                    </MyAppBar>
                    <Grid container spacing={0} direction="row" justify="flex-start" alignItems="flex-start">
                        <Hidden mdUp>
                            <Grid item xs={12}>
                                <div className="cardDetails">
                                    <h3>Details:</h3>
                                    <p>{item.description}</p>
                                    <hr />
                                    {checkGame(item)}
                                    <p><strong>Made with: </strong></p>
                                    {checkSoftware(item.software)}
                                </div>
                            </Grid>
                        </Hidden>
                        <Grid className='murkyBG' item md={9} xs={12} >
                            {CheckDisplayType(item)}
                        </Grid>
                        <Hidden smDown>
                            <Grid item md={3}>
                                <div className="cardDetails">
                                    <h3>Details:</h3>
                                    <p>{item.description}</p>
                                    <hr />
                                    {checkGame(item)}
                                    <p><strong>Made with: </strong></p>
                                    {checkSoftware(item.software)}
                                </div>
                            </Grid>
                        </Hidden>
                    </Grid>
                </Dialog>
            </div>
        );
    }
    else return null;
}

function checkSoftware(array) {
    return (
        array.map((program, index) => <div key={program + index} style={{ display: 'flex', flexDirection: 'row', justifyContent: 'initial', margin: '3px' }}>
            <SvgIcon className="softIcon darkIcon">{program}</SvgIcon><span>{program.props.name}</span>
        </div>)
    );
}

function checkGame(item) {
    if (item.type === 'Game') {
        return (
            <div>
                <p><strong>Genre: </strong>{item.genre}</p>
                <p><strong>Role: </strong>{item.role}</p>
                {item.staff !== "" && <div>
                    <p><strong>Rest of the team: </strong></p>
                    {item.staff.map((contact, index) => <div  key={contact}>
                        <p>{contact}</p>
                    </div>)}
                </div>}
            </div>
        );
    }
    else if (item.type === 'Web' || item.type === 'Web single'){
        return (
            <div>
                <h3>{item.link}</h3>
            </div>
        );
    }
    else return null;
}

function CheckDisplayType(item) {
    console.log(item.model);
    if (item.type === 'Image' || item.type === 'Web single') return <ImageInspector img={item.img} title={item.title} />
    else if (item.type === 'Gif') return <ImageInspector img={item.gif} title={item.title} />
    else if (item.type === '3D') return <Render3D model={item.model} />
    else if (item.type === 'Image Gallery' || item.type === 'Game' || item.type === 'Web') return <ImageGallery gallery={item.gallery} />
}