import React from 'react';
import * as BABYLON from '@babylonjs/core';
import SceneComponent from './BabylonSceneComponent'; // ^^ point to file we created above or 'babylonjs-hook' NPM.
import { GLTFFileLoader } from 'babylonjs-loaders';
import { OBJFileLoader } from 'babylonjs-loaders';
import 'babylonjs-loaders';

const onSceneReady = (scene, meshFile) => {
    scene.getEngine().getRenderingCanvas();
    scene.createDefaultCameraOrLight(true, false, true);
    // This loads the model
    BABYLON.SceneLoader.RegisterPlugin(new GLTFFileLoader());
    BABYLON.SceneLoader.RegisterPlugin(new OBJFileLoader());

    let rootUrl = process.env.PUBLIC_URL + '/3D/';
    let meshName = meshFile;

    BABYLON.SceneLoader.ImportMeshAsync(null, rootUrl, meshName, scene).then(results => {
        //Camera settings
        scene.createDefaultCameraOrLight(true, true, true);
        //scene.createDefaultEnvironment();
        scene.clearColor = new BABYLON.Color4(.0508, .1055, .1016, 1);
        scene.activeCamera.useFramingBehavior = true;
        var framingBehavior = scene.activeCamera.getBehaviorByName("Framing");
        framingBehavior.framingTime = 0;
        framingBehavior.elevationReturnTime = -1;
        var worldExtends = scene.getWorldExtends();
        scene.activeCamera.lowerRadiusLimit = null;
        framingBehavior.zoomOnBoundingInfo(worldExtends.min, worldExtends.max);

        // This creates a light, aiming 0,1,0 - to the sky (non-mesh)
        var light = new BABYLON.HemisphericLight("light", new BABYLON.Vector3(0, 1, 0), scene);

        // Default intensity is 1. Let's dim the light a small amount
        light.intensity = 0.7;

        var model = results.meshes[0];
        model.rotationQuaternion = null;
        model.rotation.y += Math.PI; // we're backwards in import
        model.rotation.x += Math.PI;
        model.rotation.z += Math.PI;

    });
}

/**
 * Will run on every frame render.
 */
const onRender = scene => {
}

export default (props) => (
    <div style={{ height: '91.5vh', margin: 'auto' }}>
        <SceneComponent antialias onSceneReady={onSceneReady} onRender={onRender} model={props.model} id='renderCanvas' />
    </div>
)