import React from 'react';
import { TransformWrapper, TransformComponent } from "react-zoom-pan-pinch";

export default function ImageInspector(props) {
    return (
        <TransformWrapper>
            <TransformComponent >
                <div style={{ height: '91.5vh', margin: 'auto' }}>
                    <img src={props.img} alt={props.title} style={{ height: '100%', width: '100%', objectFit: 'contain' }} />
                </div>
            </TransformComponent>
        </TransformWrapper>
    );
}