import React from 'react';
import Grid from '@material-ui/core/Grid';
//ICONS
import MailIcon from '@material-ui/icons/Mail';
import TwitterIcon from '@material-ui/icons/Twitter';
import LinkedInIcon from '@material-ui/icons/LinkedIn';
import FacebookIcon from '@material-ui/icons/Facebook';

export default function Contact() {

    function contactOption(url) {
        window.open(url, '_blank', 'noopener noreferrer');
    }

    return (
        <section id="contact" className='paperBG'>
            <h1>CONTACT ME</h1>
            <p>Feel free to do it!</p>
            <div style={{ padding: '0 10%' }}>
                <Grid container spacing={10} justify='space-between'>
                    <Grid item md={3} xs={6}>
                        <div className='card clickableCard' onClick={function(){contactOption("mailto:legal.mikael@gmail.com")}}>
                            <MailIcon style={{ fontSize: 40, position: 'relative', top: '10px', margin: '0 5px' }} />
                            <h3 style={{ display: 'inline-block', margin: '0' }}>E-Mail</h3>
                            {/*<hr/>
                            <p>Definitely the best way to be seen. It might take me a couple of days to answer <em>(since I tend to leave the answering "for later")</em>, but I will eventually! Promise!</p>*/}
                        </div>
                    </Grid>
                    <Grid item md={3} xs={6}>
                        <div className='card clickableCard' onClick={function(){contactOption("https://www.linkedin.com/in/mikael-le-gal-a4a758196")}}>
                            <LinkedInIcon style={{ fontSize: 40, position: 'relative', top: '10px', margin: '0 5px' }} />
                            <h3 style={{ display: 'inline-block', margin: '0' }}>Linked In</h3>
                            {/*<hr/>
                            <p>It is not my main way of messaging with people, but I check my profile every now and then. Send me a request to connect with me as well! The more, the merrier!</p>*/}
                        </div>
                    </Grid>
                    <Grid item md={3} xs={6}>
                        <div className='card clickableCard' onClick={function(){contactOption("https://twitter.com/KaeldirArt")}}>
                            <TwitterIcon style={{ fontSize: 40, position: 'relative', top: '10px', margin: '0 5px' }} />
                            <h3 style={{ display: 'inline-block', margin: '0' }}>Twitter</h3>
                            {/*<hr/>
                            <p>I'm fairly often there, either uploading some artwork or shitposting. It's a safe bet if you want to reach out to me!</p>*/}
                        </div>
                    </Grid>
                    <Grid item md={3} xs={6}>
                        <div className='card clickableCard' onClick={function(){contactOption("https://www.facebook.com/mikael.legalbeneroso")}}>
                            <FacebookIcon style={{ fontSize: 40, position: 'relative', top: '10px', margin: '0 5px' }} />
                            <h3 style={{ display: 'inline-block', margin: '0' }}>Facebook</h3>
                            {/*<hr/>
                            <p>Not your safest bet in order to contact me since I rarely visit Facebook but, if the rest of the options don't work for you, you might as well try!</p>*/}
                        </div>
                    </Grid>
                </Grid>
            </div>
        </section>
    );
}