import React from 'react';
import Grid from '@material-ui/core/Grid';

export default function Others() {
    return (
        <section id='others'>
            <h1>Skills</h1>
            <div style={{ padding: '0 20px' }}>
                <Grid container spacing={5} justify='space-evenly' alignContent='stretch'>
                    <Grid item md={3} xs={4}>
                        <div className='card paperBG'>
                            <h3>Digital art</h3>
                            <hr />
                            <p>This is one of my strongest creative skills and the one I have the most experience with. My favourite softwares for this are <strong>Photoshop</strong> and <strong>Clip Studio Paint</strong>, but I have also used <strong>Krita</strong>, <strong>Corel Painter</strong>, and others.</p>
                        </div>
                    </Grid>
                    <Grid item md={3} xs={4}>
                        <div className='card paperBG'>
                            <h3>Graphic Design</h3>
                            <hr />
                            <p>Although related to digital art, I consider Graphic Design to be a beast of its own kind. When working in this field, Adobe's <strong>Photoshop</strong>, <strong>Illustrator</strong>, and <strong>InDesign</strong> have proven to be the most reliable for me.</p>
                        </div>
                    </Grid>
                    <Grid item md={3} xs={4}>
                        <div className='card paperBG'>
                            <h3>Game Development</h3>
                            <hr />
                            <p>Either as a solo developer for personal projects or as co-programmer and artist in small development teams. I have mostly used <strong>Unity</strong>, but also other game engines such as <strong>Godot</strong> and <strong>Game Maker</strong>, among others.</p>
                        </div>
                    </Grid>
                    <Grid item md={3} xs={4}>
                        <div className='card paperBG'>
                            <h3>Programming</h3>
                            <hr />
                            <p>I have programmed mostly for game and web development. The languages that I have used in the past are <strong>C#</strong>, <strong>JavaScript</strong>, and <strong>GDScript</strong> (Godot's own version of Python).</p>
                        </div>
                    </Grid>
                    <Grid item md={3} xs={4}>
                        <div className='card paperBG'>
                            <h3>Front-End</h3>
                            <hr />
                            <p>Although I have used web design mostly for personal purposes, I have experience using <strong>HTML</strong>, <strong>CSS</strong>, and <strong>JavaScript</strong> and some of its most popular libraries, like <strong>React</strong>, <strong>Bootstrap</strong>, and <strong>Babylon</strong>.</p>
                        </div>
                    </Grid>
                    <Grid item md={3} xs={4}>
                        <div className='card paperBG'>
                            <h3>Animation</h3>
                            <hr />
                            <p>Mainly frame-by-frame and rig-based 2D animation. For the first one I have used <strong>Clip Studio Paint</strong> and <strong>Krita</strong>, and for the second one I have used <strong>Spriter Pro</strong> and <strong>Creature</strong>, and I'm really interested in trying <strong>Spine</strong> in the future.</p>
                        </div>
                    </Grid>
                    <Grid item md={3} xs={4}>
                        <div className='card paperBG'>
                            <h3>3D Modeling</h3>
                            <hr />
                            <p>Although I tend to default to 2D art, I have also experimented with 3D means. <strong>Blender</strong> and <strong>ZBrush</strong> are my programs of choice.</p>
                        </div>
                    </Grid>
                    <Grid item md={3} xs={4}>
                        <div className='card paperBG'>
                            <h3>Video Edition</h3>
                            <hr />
                            <p>Mostly to make motion graphics and for personal purposes, I have used <strong>Sony Vegas</strong> and <strong>Adobe Premiere</strong>, accompanied by <strong>After Effects</strong>.</p>
                        </div>
                    </Grid>
                </Grid>
            </div>
            <h2>Languages:</h2>
            <div>
                <Grid container spacing={0} justify='space-evenly' alignContent='stretch'>
                    <Grid item md={1} xs={4}>
                        <div className='card paperBG'>
                            <h3>French</h3>
                            <hr />
                            <p>Basic</p>
                        </div>
                    </Grid>
                    <Grid item md={2} xs={4}>
                        <div className='card paperBG'>
                            <h3>English</h3>
                            <hr />
                            <p>Fluent</p>
                        </div>
                    </Grid>
                    <Grid item md={3} xs={4}>
                        <div className='card paperBG'>
                            <h3>Spanish</h3>
                            <hr />
                            <p>Mother language</p>
                        </div>
                    </Grid>
                    <Grid item md={2} xs={4}>
                        <div className='card paperBG'>
                            <h3>Swedish</h3>
                            <hr />
                            <p>Currently learning!</p>
                        </div>
                    </Grid>
                    <Grid item md={1} xs={4}>
                        <div className='card paperBG'>
                            <h3>German</h3>
                            <hr />
                            <p>Basic</p>
                        </div>
                    </Grid>
                </Grid>
            </div>
            <h2>Hobbies:</h2>
            <div style={{ padding: '0 20px' }}>
                <Grid container spacing={5} justify='space-evenly' alignContent='stretch'>
                    <Grid item md={4} xs={4}>
                        <div className='card paperBG'>
                            <h3>Cooking</h3>
                            <hr />
                            <p>While for baking I have to pay attention to measurements and timing, regular cooking lets me be much more creative and that's why I love it!</p>
                        </div>
                    </Grid>
                    <Grid item md={4} xs={4}>
                        <div className='card paperBG'>
                            <h3>Reading</h3>
                            <hr />
                            <p>I have periods of time where I will devour books (figuratively, of course). My favourite genres? Historical fiction and fantasy.</p>
                        </div>
                    </Grid>
                    <Grid item md={4} xs={4}>
                        <div className='card paperBG'>
                            <h3>Running</h3>
                            <hr />
                            <p>Sometimes you need to exercise a little to avoid fusing with your chair, and my sport of choice is running.</p>
                        </div>
                    </Grid>
                    <Grid item md={4} xs={4}>
                        <div className='card paperBG'>
                            <h3>Games</h3>
                            <hr />
                            <p>I'm sure you're very surprised. Playing and making games, from board games to video games, has always been my favourite pastime, especially when friends are involved.</p>
                        </div>
                    </Grid>
                    <Grid item md={4} xs={4}>
                        <div className='card paperBG'>
                            <h3>Listening to music</h3>
                            <hr />
                            <p>Sometimes I just like letting my mind wander while I listen to some music. My favourite genres change with time, but I've been listening to a lot of folk and instrumental music lately.</p>
                        </div>
                    </Grid>
                </Grid>
            </div>
        </section>
    );
}