import React from 'react';
import Grid from '@material-ui/core/Grid';
import { styled } from '@material-ui/core/styles';
import Drawer from '@material-ui/core/Drawer';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import CssBaseline from '@material-ui/core/CssBaseline';
import List from '@material-ui/core/List';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import ArrowBackIcon from '@material-ui/icons/ArrowBack';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
//ICONS
import MailIcon from '@material-ui/icons/Mail';
import InfoIcon from '@material-ui/icons/Info';
import BrushIcon from '@material-ui/icons/Brush';
import VideogameAssetIcon from '@material-ui/icons/VideogameAsset';
//AVATAR
import Avatar from "../images/2D Art/avatar.png"

const MyAppBar = styled(AppBar)({
    background: 'linear-gradient(45deg, #007eff 30%, #220c10 75%)',
    border: 0,
    boxShadow: '0 3px 5px 2px rgba(34, 12, 16, .3)',
    color: 'white',
    padding: '0 30px',
});

export default function PersistentDrawerRight() {

    const [open, setOpen] = React.useState(false);

    const handleDrawer = () => {
        open ? setOpen(false) : setOpen(true);
    };

    return (
        <div>
            <CssBaseline />
            <MyAppBar
                position="fixed"
            >
                <Toolbar>
                    <Grid container justify="space-between" alignItems="center">
                        <Grid item>
                            <img id="webIcon" src={Avatar} alt="Web Icon" />
                        </Grid>
                        <Grid item>
                            <IconButton
                                color="inherit"
                                aria-label="open drawer"
                                edge="end"
                                onClick={handleDrawer}
                            >
                                <MenuIcon />
                            </IconButton>
                        </Grid>
                    </Grid>


                </Toolbar>
            </MyAppBar>
            <Drawer
                variant="persistent"
                anchor="right"
                open={open}
            >
                <div className='paperBG' style={{padding: '5% 3%', height:'100%'}}>
                    <div style={{ justifyContent: 'flex-start', width: '20%' }}>
                        <IconButton onClick={handleDrawer} >
                            {<ArrowBackIcon />}
                        </IconButton>
                    </div>
                    <hr />
                    <List>
                        {['About me', 'Portfolio', 'Skills & hobbies'].map((text, index) => (
                            <ListItem button key={text} onClick={(e) => { jumpToSection(e); handleDrawer(); }}>
                                <ListItemIcon>{[<InfoIcon />, <BrushIcon />, <VideogameAssetIcon />][index]}</ListItemIcon>
                                <ListItemText primary={text} />
                            </ListItem>
                        ))}
                    </List>
                    <hr />
                    <List>
                        {['Contact me'].map((text, index) => (
                            <ListItem button key={text} onClick={(e) => { jumpToSection(e); handleDrawer(); }}>
                                <ListItemIcon>{<MailIcon />}</ListItemIcon>
                                <ListItemText primary={text} />
                            </ListItem>
                        ))}
                    </List>
                </div>
            </Drawer>
        </div>
    );
}

function jumpToSection(el) {
    var tagName = el.target.innerHTML;
    var section = null;
    if (tagName === 'About me') section = document.querySelector("#about");
    else if (tagName === 'Portfolio') section = document.querySelector("#portfolio");
    else if (tagName === 'Skills &amp; hobbies') section = document.querySelector("#others");
    else if (tagName === 'Contact me') section = document.querySelector("#contact");
    section !== null && section.scrollIntoView({ behavior: "smooth", block: "start" });
}