import React, { useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import GridList from '@material-ui/core/GridList';
import GridListTile from '@material-ui/core/GridListTile';
import ImageInspector from './ImageInspector';
import ArrowBackIcon from '@material-ui/icons/ArrowBack';
import Fab from '@material-ui/core/Fab';
import { styled } from '@material-ui/core/styles';

makeStyles((theme) => ({
    root: {
        display: 'flex',
        flexWrap: 'wrap',
        justifyContent: 'space-around',
        overflow: 'hidden',
        backgroundColor: theme.palette.background.paper,
    },
    gridList: {
        width: 500,
        height: 450,
        // Promote the list into his own layer on Chrome. This cost memory but helps keeping high FPS.
        transform: 'translateZ(0)',
    },
    titleBar: {
        background:
            'linear-gradient(to bottom, rgba(0,0,0,0.7) 0%, ' +
            'rgba(0,0,0,0.8) 60%, rgba(0,0,0,0) 100%)',
    },
    icon: {
        color: 'white',
    },
}));

const MyBackButton = styled(Fab)({
    background: '#ba3f1d',
    border: 0,
    boxShadow: '0 3px 5px 2px rgba(34, 12, 16, .3)',
    color: 'white',
    position: "absolute",
    top: '1%',
    left: '1%',
});

export default function ImageGallery(props) {
    const [zoomedImage, setZoomedImage] = useState({ state: false, image: null });

    function changeState(img) {
        if (zoomedImage.state === false) {
            setZoomedImage({
                state: true,
                image: img,
            });
        }
        else {
            setZoomedImage({
                state: false,
                image: img,
            });
        }
    }

    if (zoomedImage.state === false) {
        return (
            <div style={{ height: '91.5vh' }}>
                <GridList cellHeight='auto' spacing={5} cols={Math.ceil(props.gallery.length / 2)} style={{ margin: '0' }}>
                    {props.gallery.map((img, index) => (
                        <GridListTile key={img + index} cols={1} rows={1}>
                            <img src={img} alt={"Image " + index} onClick={function () { changeState(img) }} style={{ height: '44.25vh', width: '100%', objectFit: 'cover', margin: '0' }} />
                        </GridListTile>
                    ))}
                </GridList>
            </div>
        );
    }
    else {
            return (
                <div style={{position: 'relative'}}>
                    <ImageInspector img={zoomedImage.image} title={"Zoomed image"} />
                    <MyBackButton aria-label="back" onClick={function(){changeState(null)}}>
                        <ArrowBackIcon />
                    </MyBackButton>
                </div>
    
            );
    }
}