import React from "react";
import Grid from '@material-ui/core/Grid';
import Hidden from '@material-ui/core/Hidden';
import CV from '../images/CV/CV.pdf';
import sideImg from '../images/side.png';

export default function About() {
    return (
        <section id="about">
            <Grid container spacing={0} direction="row" justify="space-evenly" alignItems="stretch">
                <Grid item xs={12}>
                    <h1>ABOUT ME</h1>
                </Grid>
                <Grid className="paperBG infoCard" item md={6} xs={12}>
                    <h3>Who am I?</h3>
                    <p>I'm a detail-oriented person with a weakness for any form of creative expression <em>(art, games, design... You name it)</em>.</p>
                    <p>I also absolutely love learning new things, especially when they are related to creative skills. This leads me to sign up for a ton of courses and go through resources online to learn new skills and software, only to jump to the next one after I'm done. What can I say, I really enjoy the <em>"aha"</em> moment when everything starts making sense.</p>
                    <p>Apart from that, I'm a pretty relaxed, easy-going guy. I tend to be on the shy side when I first meet someone, but after I get to know them I open up pretty quickly and start joking around!</p>
                    <h3>Education</h3>
                    <p>Besides my countless hours spent learning things online, I have a <strong>bachelor's degree in Architectural Technology</strong> from the <a href='https://web.ua.es/en/grados/grado-en-arquitectura-tecnica/' target='_blank' rel="noopener noreferrer">University of Alicante</a>. I also moved relatively recently to Sweden and got my <strong>master's degree in Informatics</strong> at the <a href='https://www.his.se/en/education/informatics/' target='_blank' rel="noopener noreferrer">University of Skvde</a>.</p>
                    <h3>Previous work</h3>
                    <p>Most of my professional background has been working as a freelance.Both as a <strong>graphic designer and illustrator</strong> producing graphical elements such as album covers, music festival posters and paraphernalia, motion graphics videos, etc. for small local businesses, and as a <strong>private tutor</strong> teaching English, mathematics, and technical drawing, among other subjects.</p>
                    <p>I have also worked as a camp counselor and store clerk during summer breaks. For more detailed and formal information, feel free to check <a href={CV} target='_blank' rel="noopener noreferrer">my CV</a>.</p>
                </Grid>
                <Hidden smDown>
                    <Grid item md={6}>
                        <div className="placeholderImg">
                            <img className='headerImg' src={sideImg} alt='Side decoration'></img>
                        </div>
                    </Grid>
                </Hidden>
            </Grid>
        </section>
    );
}