import React from "react";
import Avatar from "../images/2D Art/avatar.png"
import HeaderImg from "../images/header.png";


function introMessage() {
    var time = new Date().getHours();
    if (time >= 6 && time < 12) return "Good morning! How about a nice cup of coffee?";
    else if (time >= 12 && time < 18) return "Good afternoon!";
    else if (time >= 18 && time < 24) return "Good evening!";
    else if (time >= 0 && time < 6) return "Welcome to my page, fellow night owl!";
}

export default function IntroCard() {
    return (
        <section id="introCard" className="placeholderImg">
            <img className='headerImg' src={HeaderImg} alt='Header background'></img>
            <div className='introInfo'>
                <h1 className='borderText'>Mikael Le Gal</h1>
                <img id="introAvatar" src={Avatar} alt="Selfportrait" />
                <h3 className='borderText'>{introMessage()}</h3>
            </div>
        </section>
    );
}