import React from "react";
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import GridList from '@material-ui/core/GridList';
import GridListTile from '@material-ui/core/GridListTile';
import GridListTileBar from '@material-ui/core/GridListTileBar';
import SvgIcon from '@material-ui/core/SvgIcon';
import CardDetail from './CardDetail';
import Hidden from '@material-ui/core/Hidden';
//Arrays for the gridlists
import { GraphicDesign, Illustration, ConceptArt, Animation, GameDesign, Model3D, Web } from "./GridListArrays";
//Icons
import ArrowLeft from '@material-ui/icons/KeyboardArrowLeft';
import ArrowRight from '@material-ui/icons/KeyboardArrowRight';
import ArrowDown from '@material-ui/icons/KeyboardArrowDown';
import ArrowUp from '@material-ui/icons/KeyboardArrowUp';

const useStyles = makeStyles((theme) => ({
    root: {
        display: 'flex',
        flexWrap: 'wrap',
        justifyContent: 'space-around',
        overflow: 'hidden',
        backgroundColor: theme.palette.background.paper,
    },
    gridList: {
        width: 500,
        height: 450,
        // Promote the list into his own layer on Chrome. This cost memory but helps keeping high FPS.
        transform: 'translateZ(0)',
    },
    titleBar: {
        background:
            'linear-gradient(to bottom, rgba(0,0,0,0.7) 0%, ' +
            'rgba(0,0,0,0.8) 60%, rgba(0,0,0,0) 100%)',
    },
    icon: {
        color: 'white',
    },
}));

export default function Showcase(props) {
    const classes = useStyles();

    const showcaseArray = function () {
        if (props.skillName === 'Graphic Design') return GraphicDesign;
        else if (props.skillName === 'Illustration') return Illustration;
        else if (props.skillName === 'Concept Art') return ConceptArt;
        else if (props.skillName === 'Animation') return Animation;
        else if (props.skillName === '3D Modelling') return Model3D;
        else if (props.skillName === 'Game Development') return GameDesign;
        else if (props.skillName === 'Web Development') return Web;
        //Keep adding cases
    }

    //Card Detail stuff
    const [open, setOpen] = React.useState(false);
    const [detailCard, setDetailCard] = React.useState(null);
    const [showcaseState, changeShowcaseState] = React.useState(false);

    const handleClickOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };

    function toggleDisplay() {
        if (showcaseState === false) {
            changeShowcaseState(true);
        }
        else {
            changeShowcaseState(false);
        }
    }

    return (
        <div>
            <Grid className={props.titleLeft ? "darkRedBG" : "paperBG"} container spacing={0} direction="row" justify="center" alignItems="center" style={{ margin: '10px auto 10px' }}>
                {props.titleLeft && <Hidden smDown><Grid className="skillTitle infoCard" item md={showcaseState === true ? 4 : 12} onClick={toggleDisplay} container spacing={0} direction="row" justify="center" alignItems="center" style={{ margin: '10px auto 10px' }}> <Grid item md={11}><h2 style={{ margin: 0 }}>{props.skillName}</h2><hr className='hrLight' /><p>{props.description}</p></Grid><Grid item md={1}>{showcaseState === true ? <ArrowRight style={{height:'100%',width:'100%'}}/> : <ArrowLeft style={{height:'100%',width:'100%'}}/>}</Grid> </Grid></Hidden>}
                <Hidden mdUp>
                    <Grid className="skillTitle infoCard" item xs={12} onClick={toggleDisplay} container spacing={0} direction="column" justify="center" alignItems="center" style={{ margin: '10px auto 10px' }}> <Grid item><h2 style={{ margin: 0 }}>{props.skillName}</h2><hr className={props.titleLeft && 'hrLight'} /><p>{props.description}</p></Grid><Grid item>{showcaseState === true ? <ArrowUp style={{height:'50px',width:'100%'}}/> : <ArrowDown style={{height:'50px',width:'100%'}}/>}</Grid> </Grid>
                </Hidden>
                {<Grid className={showcaseState === true ? "showcaseDisplay" : "showcaseDisplay deployed"} item md={8} xs={12} style={{maxWidth: showcaseState === true ? "100%" : 0}}>
                    <GridList className={showcaseState === true ? "showcaseDisplay" : "showcaseDisplay deployed"} cellHeight={props.cellHeight} spacing={3} cols={props.cols} style={{ maxHeight: showcaseState === true ? 600 : 0,  overflow: 'auto', margin: '0' }}>
                        {showcaseArray().map((tile) => (
                            <GridListTile className="gridListItem" key={tile.img} cols={tile.featured ? 2 : 1} rows={2} onClick={function () {
                                handleClickOpen();
                                setDetailCard({
                                    tile
                                });
                            }}>
                                <img src={tile.thumbnail ? tile.thumbnail : tile.img} alt={tile.title} style={{width:'100%', height: '100%', objectFit: 'cover'}}/>
                                <GridListTileBar
                                    title={<div style={{ position: 'absolute', top: '10px', margin: '0 5px' }}>{tile.title}</div>}
                                    titlePosition="top"
                                    actionIcon={
                                        tile.software.map((program, index) => (
                                            <SvgIcon className="softIcon whiteIcon" key={tile.img + index} aria-label={program}>
                                                {program}
                                            </SvgIcon>
                                        ))}
                                    actionPosition="left"
                                    className={classes.titleBar}
                                />
                            </GridListTile>
                        ))}
                    </GridList>
                </Grid>}
                {!props.titleLeft && <Hidden smDown><Grid className="skillTitle infoCard" item md={showcaseState === true ? 4 : 12} xs={12} onClick={toggleDisplay} container spacing={0} direction="row" justify="center" alignItems="center" style={{ margin: '10px auto 10px' }}> <Grid item md={1}>{showcaseState === true ? <ArrowLeft style={{height:'100%',width:'100%'}}/> : <ArrowRight style={{height:'100%',width:'100%'}}/>}</Grid> <Grid item md={11}><h2 style={{ margin: 0 }}>{props.skillName}</h2><hr /><p>{props.description}</p></Grid> </Grid></Hidden>}
            </Grid>
            <CardDetail open={open} handleClose={handleClose}
                item={detailCard}
            />
        </div>
    );
}