import React from "react";
import Showcase from "./Showcase";

export default function Skills() {
    return (
        <section id="portfolio">
            <h1>Portfolio</h1>
            <div className='murkyBG'>
                <Showcase skillName='Graphic Design' titleLeft={true} cols={3} cellHeight={200} description="These are some of the previous work I had the chance to make as a freelance graphic designer. They're a mix of both digital and printed media." />
                <Showcase skillName='Illustration' titleLeft={false} cols={3} cellHeight={200} description="My favorite thing to do. Either for personal projects or for work, I have always dedicated time to illustrating. These are some of the pieces I've enjoyed the most." />
                {/*<Showcase skillName='Concept Art' titleLeft={true} cols={3} cellHeight={200} description="" />*/}
                <Showcase skillName='Animation' titleLeft={true} cols={3} cellHeight={200} description="Linked to illustration, I also enjoy bringing some of my creations to life with animation (mostly traditional, frame to frame animation)." />
                {/*<Showcase skillName='3D Modelling' titleLeft={true} cols={4} cellHeight={200} description="A more recent passion of mine, web development has proven to be a source of both challenge and motivation." />*/}
                <Showcase skillName='Game Development' titleLeft={false} cols={4} cellHeight={165} description="Up to this date, my game development projects have always been for personal or academic use. Nevertheless, I really love working on this field since it combines artistic creativity with some puzzle-solving mind-breaking programming!" />
                <Showcase skillName='Web Development' titleLeft={true} cols={3} cellHeight={200} description="A more recent passion of mine, web development has proven to be a source of both challenge and motivation." />
            </div>
        </section>
    );
}